.background {
    display: block;
    width: 100%;
    height: 100%;
    border-radius: inherit;
    object-position: center;
    object-fit: cover;
    image-rendering: auto
}

.backgroundContainer {
    position: absolute;
    border-radius: inherit;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: -2;
}

.avatarContainer {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 10px;
    backdrop-filter: blur(50px);
    border-radius: 10px;
    padding: 10px 20px;
}

.body {
    display: flex;
    flex-direction: column;
    position: relative;
    padding: 10px;
    height: 100vh;
    box-sizing: border-box;
}

.avatarAndWallet {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.wallet,
.buttonNav>a {
    border: 2px solid #3db268;
    backdrop-filter: blur(50px);
    border-radius: 999px;
    text-decoration: none;
    color: unset;
    color: #000;
    background: #fff;
    padding: 5px 12.5px;
    font-weight: 600;
    min-width: auto;
    font-size: 22px;
    transition: all .3s;
}

.buttonNav * {
    color: #000;
}

.wallet {
    font-size: 22px;
}

.wallet {
    overflow: hidden;
    white-space: nowrap;
}

.buttonNav {
    position: relative;
    display: flex;
    flex-direction: row;
    gap: 10px;
    justify-content: center;
    align-items: center;
}

.centerImg {
    display: flex;
    position: relative;
    justify-content: center;
    align-items: flex-start;
}

.centerImg .fog {
    position: relative;
    z-index: 1;
    width: calc(100vw - 50px);
    max-width: 512px;
    max-height: 100%;
}

.title {
    font-size: 24px;
}

.avatarContainer>img {
    max-width: 64px;
    border-radius: 999px;
}

.rayContainer {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
}

.ray1 {
    max-width: 250px;
    width: calc(50vw);
    position: absolute;
    z-index: 2;
    left: -50%;
    animation: ray1 1.5s infinite alternate ease-in-out;
}

.buttonNav>a:hover {
    transform: rotate(-5deg) scale(1.1);
    background: #3fec7e;
}

.lSide {
    animation: lMove 1s 1s forwards;
}

.rSide {
    animation: rMove 1s 1s forwards;
}

@keyframes rMove {
    0% {
        right: 0;
    }

    100% {
        right: 50vw;
        transform: translateX(50%) translateY(50%);
    }
}

@keyframes lMove {
    0% {
        left: 0;
    }

    100% {
        left: 50vw;
        transform: translateX(-50%) translateY(50%);
    }
}

@keyframes ray1 {
    0% {
        transform: translateX(140%) translateX(-80%) rotate(90deg);
    }

    100% {
        transform: translateX(360%) translateX(-80%) rotate(90deg);
    }
}


@media (max-width: 512px) {
    .buttonNav>a {
        font-size: 12px !important;
    }

    .avatarAndWallet * {
        font-size: 12px;
        font-weight: 800;
    }

    .avatarContainer>img {
        max-width: 32px;
    }
}

.logo {
    position: fixed;
    max-width: 256px;
    width: 50vw;
    bottom: 50%;
    z-index: 3;
    transform: translateY(50%);
    left: 0;
}

.cpp {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    flex: 1;
    bottom: 50px;
}

.ygo {
    position: fixed;
    max-width: 0;
    z-index: 5;
    animation: ygo 4.0s 2.0s;
    transform: translateX(-50%) translateY(-50%) rotate(0deg);
}

@keyframes ygo {
    0% {
        max-width: 0;
        top: 0;
        left: 0;
    }

    60% {
        max-width: 300px;
        top: 50%;
        left: 50%;
    }

    80% {
        left: 25vw;
        top: 70vh;
    }

    100% {
        max-width: 0;
        top: 100%;
        left: 100%;
    }
}

.bubble {
    position: absolute;
    max-width: 64px;
    transform: translateX(-70%);
    animation: bubble1 infinite 1.5s alternate ease-in-out;
    z-index: 2;
}

.bubble1 {
    position: absolute;
    max-width: 32px;
    z-index: 2;
    animation: bubble2 infinite 1.5s alternate ease-in-out;
}

@keyframes bubble1 {
    0% {
        top: 0;
    }

    100% {
        top: 20px;
    }
}

@keyframes bubble2 {
    0% {
        bottom: 0;
    }

    100% {
        bottom: 20px;
    }
}