@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@400;500;600;700;800&display=swap');

* {
    margin: 0;
    padding: 0;
    font-family: 'Quicksand', 'sans-serif';
    font-weight: 500;
    color: #fff;
}

html,
body {
    overflow-x: hidden;
    background: url("https://framerusercontent.com/images/OdXqWZrJLaBlQXTrZSVNEp9W1cU.png") no-repeat left bottom fixed;
    background-size: cover;
    overflow: auto;
    overflow-x: hidden;
}